import logo from '../assets/logo.jpg'
import "../styles/BuyNow.scss"
import LanguageSelector from './LanguageSelector';
import store from "./store";
import { useSelector } from 'react-redux';
import { buyNowTextContent } from './TextData';



function BuyNow() {

  
  //getting the current language value from the store
  const storeLanguage = state => state.language;
  //Setting up hook on storeLanguage
  const currentLanguage = useSelector(storeLanguage);

    //Finding out the index for current store Language
    const buyNowTextContentIndex = buyNowTextContent.findIndex( title => title.language === currentLanguage[0].value);    
    
    return <div className = "buy-now" >
                <div className = "buy-now__button-container">
                <h2 className='buy-now__secure'>{buyNowTextContent[buyNowTextContentIndex].subtitle}</h2>
                <a href = 'https://buy.stripe.com/fZe8x6cRKfGj5OMbII'>
                    <div className = 'buy-now__buy'>{buyNowTextContent[buyNowTextContentIndex].title}</div>
                    <img src = "/images/visa.png"/>
                    <img src = "/images/mastercard.png"/>
                </a>
                
                </div>
                <i class="fas fa-lock"></i>
            </div>
    
  }
export default BuyNow